import React, {useState} from "react"
import HamburgerButton from "./HamburgerButton";
import "./component-styles/Navbar.css"
import { Link } from "gatsby"

const LINKS = [
    {
        route: "/",
        name:  "Home"
    },
    {
        route: "/test-page/",
        name: "Test"
    }
]

const TITLE = "End Me"

const Navbar = () => {
    const [isOpen, setIsOpen] = useState(false);

    const openClose = () => {
        setIsOpen(!isOpen);
    };

    const navLinks = LINKS.map(e => {
        return <div className={`transform duration-500 hover:scale-105 text-2xl my-3 mx-5 ${isOpen ? "h-full" : "h-0"}`} key={e.route}><Link className="hover:text-navy" to={e.route}>{e.name}</Link></div>
    });

    return(
        <nav className="fixed top-0 z-50 min-w-full">
            <div className="flex flex-row shadow-xl bg-white">
                <div className="transform duration-500 hover:scale-105 flex-initial text-3xl md:text-5xl font-extrabold p-5">
                    <Link className="ring ring-4 bg-clip-text text-transparent bg-gradient-to-r from-navy to-turquoise" to={"/"}>
                        {TITLE}
                    </Link>
                </div>
                <div className="transform duration-500 hover:scale-105 ml-auto mt-6 mx-5">
                    <HamburgerButton isOpen={isOpen} onClick={() => openClose()}/>
                </div>
            </div>
            <div className={`nav-table border-t-4 shadow-xl bg-white text-center font-bold ${isOpen ? "nav-table-shown" : "nav-table-hidden"}`}>
                { navLinks }
            </div>
        </nav>
    );
}

export default Navbar